<template>
  <div id="app">
    <el-container>
      <el-aside :width="fold? '60px' : '200px'" style="transition:all .6s">
        <img src="./logo.png" alt width="140" />
        <el-row class="tac">
          <el-col :span="24">
            <el-menu
              default-active="2"
              class="el-menu-vertical-demo"
              background-color="#545c64"
              text-color="#fff"
              active-text-color="#ffd04b"
              :collapse="fold"
              :collapse-transition="false"
              router
              style="border-right: 0px"
              :default-active="$route.path"
            >
              <el-menu-item index="/home" :style=" fold? 'width:60px' :'width:200px'">
                <i class="el-icon-menu"></i>
                <span slot="title">首页</span>
              </el-menu-item>
              <el-submenu index="2" :style=" fold? 'width:60px' :'width:200px'">
                <template slot="title">
                  <i class="el-icon-location"></i>
                  <span>内容管理</span>
                </template>
                <el-menu-item-group>
                  <el-menu-item index="/articleAdd">发布文章</el-menu-item>
                  <el-menu-item index="/article">内容列表</el-menu-item>
                  <el-menu-item index="2-3">评论列表</el-menu-item>
                  <el-menu-item index="/material">素材管理</el-menu-item>
                </el-menu-item-group>
              </el-submenu>
              <el-menu-item index="/fans" :style=" fold? 'width:60px' :'width:200px'">
                <i class="el-icon-menu"></i>
                <span slot="title">粉丝管理</span>
              </el-menu-item>
              <el-menu-item index="/account" :style=" fold? 'width:60px' :'width:200px'">
                <i class="el-icon-setting"></i>
                <span slot="title">用户信息</span>
              </el-menu-item>
            </el-menu>
          </el-col>
        </el-row>
      </el-aside>
      <el-container>
        <el-header>
          <el-row>
            <el-col :span="10">
              <div class="grid-content bg-purple">
                <i :class=" fold ? 'el-icon-s-unfold' :' el-icon-s-fold' " @click="fold = !fold"></i>
                <span>这是一个根据饿了么插件库写的Vue</span>
              </div>
            </el-col>
            <el-col :span="14">
              <div class="grid-content bg-purple-light">
                <el-input placeholder="请输入内容" prefix-icon="el-icon-search" v-model="input2"></el-input>
                <span id="infor">消息</span>
                <img :src="photo" alt width="40" height="40" />
                <el-dropdown trigger="click">
                  <span class="el-dropdown-link">
                    {{name}}
                    <i class="el-icon-arrow-down el-icon--right"></i>
                  </span>
                  <el-dropdown-menu slot="dropdown">
                    <el-dropdown-item>个人信息</el-dropdown-item>
                    <el-dropdown-item @click.native="togit">git地址</el-dropdown-item>
                    <el-dropdown-item @click.native="loginOut">退出</el-dropdown-item>
                  </el-dropdown-menu>
                </el-dropdown>
              </div>
            </el-col>
          </el-row>
        </el-header>
        <el-main>
          <router-view></router-view>
        </el-main>
      </el-container>
    </el-container>
  </div>
</template>

<script>
import Pass from '@/components/pass.js'
export default {
  name: 'Home',
  data () {
    return {
      input2: '',
      fold: false,
      nameData: '',
      photoData: ''
    }
  },
  methods: {
    loginOut () {
      this.$confirm('确定要提出系统?', '退出', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(() => {
          window.sessionStorage.clear()
          this.$router.push('/login')
        })
        .catch(() => {})
    },
    togit () {
      window.open('https://github.com/jiang1218')
    }
  },
  computed: {
    name () {
      return this.nameData || JSON.parse(window.sessionStorage.getItem('userinfo')).name
    },
    photo () {
      return this.photoData || JSON.parse(window.sessionStorage.getItem('userinfo')).photo
    }
  },
  created () {
    Pass.$on('pass', val => {
      this.nameData = val.data.data.name
      let user = JSON.parse(window.sessionStorage.getItem('userinfo'))
      user.name = this.nameData
      window.sessionStorage.setItem('userinfo', JSON.stringify(user))
    })
    Pass.$on('Photo', val => {
      this.photoData = val
      let user = JSON.parse(window.sessionStorage.getItem('userinfo'))
      user.photo = val
      window.sessionStorage.setItem('userinfo', JSON.stringify(user))
    })
  }

}
</script>

<style lang="less" scoped>
.el-container,
.el-aside {
  overflow: hidden !important;
}
#app {
  .el-container {
    height: 100%;
    .el-aside {
      background-color: #545c64;
      img {
        margin: 10px 20px 0px;
      }
    }
    .el-header {
      padding: 0 10px 0 10px;
      min-width: 900px;
      .grid-content {
        line-height: 60px;
        font-size: 18px;
        i {
          margin-right: 10px;
          cursor: pointer;
        }
      }
      .bg-purple-light {
        display: flex;
        justify-content: flex-end;
        line-height: 60px;
        img {
          border-radius: 50%;
          margin-top: 10px;
          margin-right: 10px;
        }
        .el-input {
          width: 180px;
          height: 34px;
        }
        #infor {
          margin: 0 20px;
        }
      }
    }
    .el-main {
      background-color: #f2f3f5;
      padding: 15px 0 0 12px;
      min-width: 900px;
    }
  }
}
.el-main {
  overflow-y: scroll;
}
.el-main::-webkit-scrollbar {
  display: none;
}
</style>
